import React, { Component } from 'react';

class Logo extends Component {
  render() {
    return (
        <div className="logo">
          <span>RECORD</span>           
          <img alt="Record Finder" src="data:image/svg+xml;utf8;base64," />
          <span>FINDER</span>          
        </div>
    );
  }
}

export default Logo;



